.header {
  justify-content: space-between;
  padding: 0 50px;
  font-size: 12px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  align-items: center;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:not(:first-child), .nav__li {
  margin-left: 20px;
}

.nav__link:first-child {
  margin-left: 0;
}

.is-active, .nav__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.main {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding-top: 50px;
  display: grid;
}

@media (width <= 487px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  height: 408px;
  transition: transform .3s;
  display: flex;
}

.card__top {
  flex-direction: column;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 32px auto auto;
}

.card__title {
  color: #060b35;
  margin: 40px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__article {
  color: #616070;
  margin: 4px 16px 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__assessment {
  box-sizing: border-box;
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.card__stars {
  margin-left: 16px;
}

.card__reviews {
  color: #060b35;
  margin-right: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.card__bottom {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 26px;
}

.card__price-num {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__stars {
  display: flex;
}

.card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 13px;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__star:last-child {
  margin-right: 0;
}

.card__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}
